import { ValidationObserver, ValidationProvider } from "vee-validate";

export default {
	components: {
		ValidationObserver,
		ValidationProvider
	},
	name: "index",
	computed: {
		img_url() {
			return process.env.apiPublicUrl;
		}
	},
	validate({ params }) {
		return params.id !== null ? params.id : null;
	},
	methods: {
		isEmpty(obj) {
			if (typeof obj == "number") return false;
			else if (typeof obj == "string") return obj.length == 0;
			else if (Array.isArray(obj)) return obj.length == 0;
			else if (typeof obj == "object")
				return obj == null || Object.keys(obj).length == 0;
			else if (typeof obj == "boolean") return false;
			else return !obj;
		},
		getRecipes(param) {
			if (this.search !== null) {
				this.$store
					.dispatch("getRecipeCategories", {
						recipeCategoriesURL:
							"recipe-categories/" +
							this.$route.params.id +
							"?page=" +
							this.pagination.current +
							"&search=" +
							decodeURIComponent(this.search)
					})
					.then(response => {
						this.recipes = this.$store.getters.recipeCategories.recipes.data;
						this.categories = this.$store.getters.recipeCategories.categories;
						this.pagination.current = this.$store.getters.recipeCategories.recipes.current_page;
						this.pagination.total = this.$store.getters.recipeCategories.recipes.last_page;
					});
			} else {
				if (param) {
					this.$store
						.dispatch("getRecipeCategories", { recipeCategoriesURL: param })
						.then(response => {
							this.recipes = this.$store.getters.recipeCategories.recipes.data;
							this.categories = this.$store.getters.recipeCategories.categories;
							this.pagination.current = this.$store.getters.recipeCategories.recipes.current_page;
							this.pagination.total = this.$store.getters.recipeCategories.recipes.last_page;
						});
				} else {
					this.$store
						.dispatch("getRecipeCategories", {
							recipeCategoriesURL:
								"recipe-categories/" +
								this.$route.params.id +
								"?page=" +
								this.pagination.current
						})
						.then(response => {
							this.recipes = this.$store.getters.recipeCategories.recipes.data;
							this.categories = this.$store.getters.recipeCategories.categories;
							this.pagination.current = this.$store.getters.recipeCategories.recipes.current_page;
							this.pagination.total = this.$store.getters.recipeCategories.recipes.last_page;
						});
				}
			}
		},
		onPageChange() {
			this.getRecipes();
		}
	},
	data() {
		return {
			categories: [],
			recipes: [],
			search: null,
			pagination: {
				current: 1,
				total: 1
			},
			siteSettings: this.$store.getters.siteSettings
		};
	},
	mounted() {
		this.getRecipes();
	}
};
